				`En las mismas islas donde muchos convictos, desertores del ejército y servidores chinos murieron recolectando guano hace un siglo y medio, grupos de trabajadores Quechua desde las montañas ahora raspan para remover abono del piso y meterlo en botes que los transporten por el continente. "Estamos excavando los últimos restos de guano en Peru" , explica Victor Ropón, 66, supervisor de la provincia de Ancash, de quien su piel curtida refleja sus años de trabajo en las Islas guano, desde sus 17 años.`
