				`Muchos andan descalzos, y cuando cambian de turno, poco después del mediodía, sus pies y pantorrillas están cubiertos de estiércol. Algunos cubren la boca y la nariz con un paño para evitar respirar el polvo de guano que, afortunadamente, es casi inodoro, aparte de un ligero olor a amoníaco. "No es una vida fácil, pero la elegí", dice Bruno Sulca, de 62 años, que supervisa la carga de bolsas de guano de Guañape desde la isla de la costa norte de Perú. Sulca y otros trabajadores ganan alrededor de 400 euros al mes, más del triple de lo que pagan los trabajadores manuales en áreas montañosas empobrecidas.`
