				`El comercio de guano en el Perú, quijóticamente, se mantiene después de estar casi agotado por la sobreexplotación. Es poco probable que vuelva a crecer nuevamente en comparación con el siglo XIX, cuando los depósitos tenían 50 pies de espesor y los ingresos de exportación representaban la mayor parte del presupuesto nacional. En la mayoría de las islas, incluida Asia, ubicada al sur de la capital, Lima, el guano alcanza menos de 30 pulgadas. Pero lo que queda aquí es codiciado cuando se ve en el contexto del frenesí en Perú y en el extranjero causado por fertilizantes sintéticos como la urea, cuyo precio se ha duplicado en el último año.`,
				`“El guano tiene la ventaja de estar libre de químicos artificiales”, explica Enrique Balmaceda, productor de mangos orgánicos en Piura, una provincia del norte del Perú. “El problema es que no hay suficiente para satisfacer la demanda, ahora que los nuevos productos como el banano orgánico compiten por lo que está disponible”.`
