				`
				Esto explica por qué Perú pone tanto esfuerzo en preservar el guano restante, un esfuerzo que se remonta a un siglo, cuando el país creó la Compañía de Administración de Guano y nacionalizó las islas, algunas de ellas controladas por los británicos, para evitar la extinción de la producción de guano. Desde entonces, el gobierno de Perú restringe la extracción a aproximadamente dos islas por año para permitir que se acumulen los excrementos.`,
				`Los administradores de guano mantienen guardias armados en cada una de las islas, para proteger a las aves. "Los pescadores son los más dañinos", dijo Rómulo Ibarra, de 40 años, uno de los dos guardias estacionados en la isla deshabitada de Asia. "Cuando te acercas a la isla, los motores asustan a los guanays", dice, refiriéndose al precioso cormorán guanay. "Y en el mar, los barcos de pesca capturan anchoveta, algo que no podemos controlar". Anchoveta es un pez de 15 centímetros que pertenece a la familia de la anchoa y es el alimento principal de las aves marinas que dejan sus excrementos en estas islas áridas.`
