				`El mayor temor de los recolectores de guano es que las flotas comerciales están agotando el stock de anchoveta, que se utiliza cada vez más como alimento de peces para aves y otros animales que ahora en Asia también está aumentando la demanda de productos cárnicos. Aunque en los últimos dos años la población de aves ha aumentado de 3.2 a 4 millones, sigue siendo una cifra insignificante en comparación con la población de 60 millones de habitantes durante el apogeo de la primera carrera de guano. Ante la disminución de las existencias de anchoveta, los gerentes de Proabonos han congelado las exportaciones de guano para garantizar un suministro interno.`
